<template>
	<view>
		<view class="about-head">
			<view class="about-logo"><image :src="logoImage" mode="aspectFit"></image></view>
			<view class="about-name">{{appName}}</view>
			<view class="about-vers">{{appVersion}}</view>
			<view class="about-info">{{appInfo.appInfo}}</view>
		</view>
		<view class="about-content">
			<view class="about-content-left">
				<view>视频合作官方邮箱</view>
				<view class="about-email">{{appInfo.videoEmail}}</view>
			</view>
			<view class="about-content-right" @click="setCopy(appInfo.videoEmail)">复制</view>
		</view>
		<view class="about-content">
			<view class="about-content-left">
				<view>广告合作官方邮箱</view>
				<view class="about-email">{{appInfo.adEmail}}</view>
			</view>
			<view class="about-content-right" @click="setCopy(appInfo.adEmail)">复制</view>
		</view>
		<view class="about-content">
			<view class="about-content-left">
				<view>洽谈合作事宜 QQ</view>
				<view class="about-email">{{appInfo.qq}}</view>
			</view>
			<view class="about-content-right" @click="setCopy(appInfo.qq)">复制</view>
		</view>
		<view class="about-content">
			<view class="about-content-left">
				<view>洽谈合作事宜微信</view>
				<view class="about-email">{{appInfo.wx}}</view>
			</view>
			<view class="about-content-right" @click="setCopy(appInfo.wx)">复制</view>
		</view> 
		<view style="height:70upx;"></view>
	</view>
</template>

<script>
import api from "@/common/api.js";
var _self;
export default {
	data() {
		return {
			appName: '已是最新版',
			appInfo: {},
			appVersion: '3.0.202011',
			logoImage: '../../static/logo.png',
		}
	},
	onLoad() {
		_self = this;
		uni.showLoading({ title:'加载中...',mask:true });
		// #ifdef APP-PLUS
		plus.runtime.getProperty(plus.runtime.appid,(e)=>{
			//console.log(e)
			_self.appName = e.name;
			_self.appVersion = e.version;
		});
		// #endif
		var imgUrl = uni.getStorageSync('appLogo');
		if(imgUrl) _self.logoImage = imgUrl;
		_self.getAbout();
	},
	methods:{
		getAbout() {
			uni.request({
				url: api.apiData.getAbout,
				method: 'GET',
				success: (e)=> {
					_self.appInfo = e.data.Data;
					uni.hideLoading();
				},
				fail: ()=> {
					// 加载失败提示
					_self.msgData('服务器连接失败，请检查网络是否正常', 5000);
				}
			});
		},
		setCopy(str) {
			api.copy(str);
		},
		skipUrl(url = '/pages/login/login', type = 1) {
			if(type == 1){
				uni.reLaunch({ url: url });
			}else{
				uni.navigateTo({ url: url });
			}
		},
		// 提示
		msgData(title = '', times = 1000, style = 'none') {
			// 加载失败提示
			uni.showToast({
				icon: style,
				title: title,
				mask: true,
				duration: times
			});
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
		_self.getAbout();
		uni.stopPullDownRefresh();
	}
}
</script>

<style>
.about-head{
	width: 100%;
	background-image: linear-gradient(to right, rgba(255,12,208,0.8), rgba(254,67,101,0.8));
	padding: 20% 0 20px;
	margin-bottom: 30px;
}
.about-content{ width:90%;margin:20px 5%; }
.about-content-left{ display:inline-block;font-size:15px;color:#666666; }
.about-email{ color:#777777;font-size:12px; }
.about-content-right{ 
	display: inline-block;
	background: #F1F1F1;
	font-size: 15px;
	float: right;
	height: 30px;
	line-height: 30px;
	width: 60px;
	text-align: center; 
	margin-top: 3px;
	border-radius: 3px;
	color: #666666;
}
.about-logo{ text-align: center; }
.about-logo image{ height:70px;border-radius:20upx; }
.about-name{ width:100%;height:30px;line-height:40px;text-align:center;color:#FFFFFF;font-size:18px; }
.about-vers{ width:100%;font-size:12px;text-align:center;height:30px;color:#F9F9F9; }
.about-info{ width:80%;padding:0 10%;color:#F9F9F9;font-size:13px;line-height:45upx; }
</style>
